<template>
  <div class="PettycashList">
    <v-card :color="$store.state.colorSet.bgColor2">
      <v-card-title>
        零用金管理
        <v-img
          src="@/assets/img/saifu_gamaguchi.png"
          aspect-ratio="2.5"
          contain
        ></v-img>

        <v-toolbar outlined rounded class="align-center">
          <v-text-field
            label="目前零用金餘額"
            class="mr-3 text-h6 error lighten-4 font-weight-bold"
            style="max-width: 200px"
            :value="filterPrice"
            outlined
            hide-details
            dense
            readonly
          ></v-text-field>
          <v-text-field
            hide-details
            prepend-icon="mdi-magnify"
            single-line
            label="搜尋"
            v-model="searchKey"
            v-if="auth.pettycash_search_key"
          ></v-text-field>

          <popupadd ref="editProcess" v-if="auth.pettycash_add_key"></popupadd>
        </v-toolbar>
      </v-card-title>
      <v-card-text>
        <v-data-iterator
          :items="searchfilter"
          :items-per-page.sync="itemsPerPage"
          :footer-props="{ itemsPerPageOptions }"
        >
          <template v-slot:default="props">
            <v-simple-table fixed-header class="text-no-wrap"  >
              <template v-slot:default>
                <thead class="title">
                  <tr>
                    <th class="text-left"></th>
                    <th class="text-left">日期</th>
                    <th class="text-left">項目名稱</th>
                    <th class="text-left">金額</th>
                    <th class="text-left">數量</th>
                    <th class="text-left">小計</th>
                    <th class="text-left">備註</th>
                    <th class="text-left">創建人員</th>
                    <th class="text-left">修改紀錄</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in props.items" :key="index">
                    <td>
                      <v-menu transition="scale-transition" offset-y>
                        <template v-slot:activator="{ on }">
                          <v-btn icon v-on="on">
                            <img src="@/assets/gear.svg" alt="" />
                          </v-btn>
                        </template>
                        <v-list>
                          <v-list-item
                            @click="edit(item)"
                            v-if="auth.pettycash_edit_key"
                          >
                            <v-list-item-title>修改</v-list-item-title>
                          </v-list-item>
                          <v-list-item
                            @click="del(item)"
                            v-if="auth.pettycash_del_key"
                          >
                            <v-list-item-title>刪除</v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>
                    </td>
                    <td>{{ item.date }}</td>
                    <td>{{ item.name }}</td>
                    <td>{{ item.price }}</td>
                    <td>{{ item.quantity }}</td>
                    <td>{{ item.price * item.quantity }}</td>
                    <td>{{ item.note }}</td>
                    <td>{{ item.create_man }}</td>
                    <td>{{ item.edit_man }}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </template>
        </v-data-iterator>
      </v-card-text>
      <!-- <pre>{{items}}</pre> -->
    </v-card>
  </div>
</template>

<script>
import popupadd from "@/views/main/PettycashAdd";
import dayjs from "dayjs";

export default {
  components: { popupadd },
  data() {
    return {
      // items: [], //目前讀取進來的資料庫
      searchKey: "",
      auth: "", //各畫面授權使用功能限定(載入放在mounted)
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
    };
  },

  mounted() {
    this.auth = this.$store.state.pData;
    this.getAllData();
  },

  computed: {
    //零用金額總計
    filterPrice() {
      return this.items.reduce((total, i) => {
        return (total += parseInt(i.price));
      }, 0);
    },
    items() {
      return this.$store.state.pettycashItems.sort((a, b) =>
        a.date < b.date ? 1 : -1
      );
    },
    searchfilter() {
      var keys = this.searchKey.split(" ");
      var str = "";

      return keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
    },
  },

  methods: {
    //取得所有資料
    getAllData() {
      this.$store.dispatch("getItems", "pettycash");
    },
    //修改功能
    edit(item) {
      this.$refs.editProcess.$emit("fromParent", item);
    },
    //刪除功能
    del(item) {
      this.yn().then((result) => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          item.delman =
            this.$store.state.pData.name +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";

          var postData = {
            snkey: item.snkey,
            tablename: "pettycash",
            datalist: JSON.stringify(item),
          };

          this.myAxios(
            "general/delv3/" +
              this.$store.state.databaseName +
              "/" +
              postData.tablename,
            postData
          ).then((rs) => {
            // console.log(rs.data);
            if (rs.state == 1) {
              this.$swal({ icon: "success", text: "已刪除!" });
              this.getAllData();
            }
          });
        }
      });
    },
  },
};
</script>


<style lang="scss">
.PettycashList {
  .theme--light.v-data-table
    > .v-data-table__wrapper
    > table
    > thead
    > tr
    > th {
    font-size: 1rem;
  }

}
</style>